import React, { useState, useEffect } from 'react';
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './Calendario.css';

function Calendario() {
  const [fechaSeleccionada, setFechaSeleccionada] = useState(new Date());
  const [eventos, setEventos] = useState([]);
  const [eventoInput, setEventoInput] = useState('');
  const [colorSeleccionado, setColorSeleccionado] = useState('#f76c9e'); // Color predeterminado

  // Localizacin de la fecha
  const localizacionEspaol = {
    locale: 'es-ES',
    options: { weekday: 'short', year: 'numeric', month: 'long', day: 'numeric' }
  };

  // Cuando seleccionamos una fecha en el calendario
  const handleDateChange = (date) => {
    setFechaSeleccionada(date);
  };

  // Agregar un evento
  const agregarEvento = () => {
    if (eventoInput.trim() === '') {
      toast.error('El nombre del evento no puede estar vaco');
      return;
    }

    const nuevoEvento = {
      id: Date.now(),
      fecha: fechaSeleccionada.toDateString(),
      nombre: eventoInput,
      color: colorSeleccionado
    };

    setEventos([...eventos, nuevoEvento]);
    setEventoInput('');
    toast.success('Evento agregado con xito ');
    programarNotificacion(nuevoEvento);
  };

  // Eliminar un evento
  const eliminarEvento = (id) => {
    const eventosActualizados = eventos.filter(evento => evento.id !== id);
    setEventos(eventosActualizados);
    toast.warn('Evento eliminado ');
  };

  // Filtrar eventos por la fecha seleccionada
  const eventosDelDia = eventos.filter(evento => evento.fecha === fechaSeleccionada.toDateString());

  // Programar notificacin para el evento
  const programarNotificacion = (evento) => {
    const eventoFecha = new Date(evento.fecha + ' ' + '09:00:00'); // Suponiendo la notificacin a las 9:00 AM
    const tiempoRestante = eventoFecha.getTime() - new Date().getTime();

    if (tiempoRestante > 0) {
      setTimeout(() => {
        toast.info(`Recordatorio: Hoy tienes el evento "${evento.nombre}"! `);
      }, tiempoRestante);
    }
  };

  useEffect(() => {
    // Programar notificaciones para todos los eventos existentes
    eventos.forEach(evento => {
      programarNotificacion(evento);
    });
  }, [eventos]);

  return (
    <div className="calendario-container">
      <h1> Calendario Personalizado</h1>

      <Calendar 
        onChange={handleDateChange} 
        value={fechaSeleccionada} 
        locale="es-ES"
        formatDay={(locale, date) => new Intl.DateTimeFormat('es-ES', { day: '2-digit' }).format(date)}
        formatShortWeekday={(locale, date) => new Intl.DateTimeFormat('es-ES', { weekday: 'short' }).format(date).toUpperCase()}
        tileContent={({ date, view }) => {
          const eventosDelDia = eventos.filter(evento => evento.fecha === date.toDateString());
          return eventosDelDia.map((evento, index) => (
            <div 
              key={index} 
              className="event-marker" 
              style={{ backgroundColor: evento.color }}
            ></div>
          ));
        }}
      />

      <h2>Agregar Evento</h2>
      <input 
        type="text" 
        placeholder="Nombre del evento" 
        value={eventoInput} 
        onChange={(e) => setEventoInput(e.target.value)} 
      />

      <input 
        type="color" 
        value={colorSeleccionado} 
        onChange={(e) => setColorSeleccionado(e.target.value)} 
      />

      <button onClick={agregarEvento} className="agregar-evento-button">
        Agregar Evento
      </button>

      <h3>Eventos del {fechaSeleccionada.toLocaleDateString('es-ES')}</h3>
      <ul className="lista-eventos">
        {eventosDelDia.map(evento => (
          <li key={evento.id} style={{ borderLeft: `10px solid ${evento.color}` }}>
            {evento.nombre} 
            <button onClick={() => eliminarEvento(evento.id)} className="eliminar-button">
              
            </button>
          </li>
        ))}
      </ul>

      <ToastContainer />
    </div>
  );
}

export default Calendario;
