import React, { useState } from 'react';
import './Login.css'; // Estilos para el login
import { Link } from 'react-router-dom'; // Importar para usar enlaces de navegacin

function Login({ onLogin }) {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();
    onLogin(username, password); 
    localStorage.setItem('username', username); // Guardar el nombre de usuario en localStorage
  };

  return (
    <div className="login-container">
      <h2>Iniciar sesin</h2>
      <form onSubmit={handleSubmit}>
        <div>
          <label htmlFor="username">Usuario:</label>
          <input
            type="text"
            id="username"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            placeholder="Introduce tu usuario"
            required
          />
        </div>
        <div>
          <label htmlFor="password">Contrasea:</label>
          <input
            type="password"
            id="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            placeholder="Introduce tu contrasea"
            required
          />
        </div>
        <button type="submit" className="login-button">Entrar</button>
      </form>

      <div className="register-link-container">
        <p>No tienes una cuenta?</p>
        <Link to="#" className="register-link">Regstrate</Link> 
      </div>
    </div>
  );
}

export default Login;
