import React from 'react';
import { Link } from 'react-router-dom';

function Crecimiento() {
  const etapas = [
    {
      titulo: "6-7 Meses",
      contenido: "Tu beb comenzar a sentarse solo, a balbucear 'mam' y 'pap' y mostrar curiosidad por objetos cercanos.",
      recomendaciones: [
        "Jugar a pasar objetos de una mano a otra.",
        "Leer libros con imgenes grandes y coloridas.",
        "Fomentar la exploracin de juguetes con diferentes texturas."
      ]
    },
    {
      titulo: "8-9 Meses",
      contenido: "El beb empieza a gatear y a pararse con apoyo. Entiende el 'no' y responde a su nombre.",
      recomendaciones: [
        "Fomenta el gateo con juguetes a distancia.",
        "Lee cuentos con sonidos para que los reconozca.",
        "Permite que manipule objetos seguros para descubrir texturas."
      ]
    },
    {
      titulo: "10-12 Meses",
      contenido: "Tu beb comenzar a caminar con apoyo, a decir algunas palabras y a sealar objetos de inters.",
      recomendaciones: [
        "Proporciona juguetes que se muevan para que los persiga.",
        "Practica juegos de imitacin y baile.",
        "Aydalo a usar cubiertos pequeos y suaves para que coma solo."
      ]
    }
  ];

  return (
    <div className="crecimiento-container">
      <button className="back-button">
        <Link to="/">Volver al inicio</Link>
      </button>

      <h2>Crecimiento del Beb</h2>
      <p>Descubre las etapas de desarrollo de tu beb y las actividades recomendadas para cada una.</p>

      {etapas.map((etapa, index) => (
        <div key={index} className="etapa">
          <h3>{etapa.titulo}</h3>
          <p>{etapa.contenido}</p>
          <h4>Recomendaciones</h4>
          <ul>
            {etapa.recomendaciones.map((recomendacion, i) => (
              <li key={i}>{recomendacion}</li>
            ))}
          </ul>
        </div>
      ))}
    </div>
  );
}

export default Crecimiento;
