import React, { useState, useEffect } from 'react';
import Confetti from 'react-confetti';
import './Logros.css';

function Logros() {
  const [logros, setLogros] = useState([
    { id: 1, nombre: 'Primer Evento', descripcion: 'Agrega tu primer evento al calendario', desbloqueado: false },
    { id: 2, nombre: 'Organizador', descripcion: 'Agrega 5 eventos al calendario', desbloqueado: false },
    { id: 3, nombre: 'Super Organizador', descripcion: 'Agrega 10 eventos al calendario', desbloqueado: false },
    { id: 4, nombre: 'Usuario Activo', descripcion: 'Inicia sesin 10 veces', desbloqueado: false },
    { id: 5, nombre: 'Fan de Ruidos Blancos', descripcion: 'Reproduce 5 sonidos relajantes', desbloqueado: false },
  ]);
  
  const [showConfetti, setShowConfetti] = useState(false);

  // Simulacin de desbloqueo de logros (esto se conecta con la lgica real)
  useEffect(() => {
    // Simulamos desbloquear algunos logros por ejemplo
    setTimeout(() => {
      desbloquearLogro(1);
    }, 2000);
  }, []);

  const desbloquearLogro = (id) => {
    const logrosActualizados = logros.map(logro => 
      logro.id === id ? { ...logro, desbloqueado: true } : logro
    );
    setLogros(logrosActualizados);
    setShowConfetti(true);
    setTimeout(() => setShowConfetti(false), 5000); 
    alert(` Has desbloqueado el logro: "${logrosActualizados.find(logro => logro.id === id).nombre}"!`);
  };

  return (
    <div className="logros-container">
      <h1> Logros</h1>

      {showConfetti && <Confetti numberOfPieces={300} />}
      
      <div className="logros-lista">
        {logros.map(logro => (
          <div 
            key={logro.id} 
            className={`logro-item ${logro.desbloqueado ? 'desbloqueado' : 'bloqueado'}`}
          >
            <h3>{logro.nombre}</h3>
            <p>{logro.descripcion}</p>
            {logro.desbloqueado 
              ? <span className="logro-estado"> Desbloqueado</span> 
              : <span className="logro-estado"> Bloqueado</span>
            }
          </div>
        ))}
      </div>
    </div>
  );
}

export default Logros;
