import React, { useState } from 'react';
import { Link } from 'react-router-dom';

function Diario() {
  const [note, setNote] = useState('');  // Estado para la nota actual
  const [notes, setNotes] = useState([]);  // Estado para la lista de notas
  const [isModalOpen, setIsModalOpen] = useState(false);  // Estado para abrir o cerrar el modal
  const [currentNote, setCurrentNote] = useState(null);  // Estado para la nota seleccionada

  // Funcin para agregar una nueva nota
  const handleAddNote = () => {
    if (note.trim() === '') {
      alert("La nota no puede estar vaca");
      return;
    }

    const newNote = {
      text: note,
      date: new Date().toLocaleString(),
    };

    setNotes([...notes, newNote]);
    setNote(''); // Limpiar el campo de texto
  };

  // Funcin para abrir una nota en un modal
  const handleOpenNote = (note) => {
    setCurrentNote(note);
    setIsModalOpen(true);
  };

  // Funcin para cerrar el modal
  const closeModal = () => {
    setIsModalOpen(false);
    setCurrentNote(null);
  };

  return (
    <div className="diario-container">
      <button className="back-button">
        <Link to="/">Volver al inicio</Link>
      </button>
      
      <h2>Notas del Diario</h2>

      {/* Campo de texto para escribir una nueva nota */}
      <textarea
        value={note}
        onChange={(e) => setNote(e.target.value)}
        placeholder="Escribe tus notas..."
      ></textarea>

      {/* Botn para agregar la nota */}
      <button onClick={handleAddNote} className="add-note-button">
        Agregar Nota
      </button>

      {/* Lista de notas */}
      <div className="notes-list">
        {notes.map((note, index) => (
          <div
            key={index}
            className="note"
            onClick={() => handleOpenNote(note)}  // Al hacer click, abrir el modal
          >
            <p>{note.text}</p>
            <small>{note.date}</small>
          </div>
        ))}
      </div>

      {/* Modal para mostrar el detalle de la nota */}
      {isModalOpen && currentNote && (
        <div className="modal">
          <div className="modal-content">
            <span className="close-button" onClick={closeModal}>&times;</span>
            <h3>Nota Detallada</h3>
            <p>{currentNote.text}</p>
            <small>{currentNote.date}</small>
          </div>
        </div>
      )}
    </div>
  );
}

export default Diario;
