import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import Confetti from 'react-confetti';
import './Premium.css';

// Importar el sonido premium
const sonidoPremium = require('./sounds/Premium.mp3');

function Premium({ setIsPremium }) {
  const [showConfetti, setShowConfetti] = useState(false);
  const navigate = useNavigate(); 
  const [audioPremium, setAudioPremium] = useState(null); 

  useEffect(() => {
    const audio = new Audio(sonidoPremium);
    setAudioPremium(audio);
  }, []);

  const handleUpgradeToPremium = () => {
    if (audioPremium) {
      audioPremium.currentTime = 0;
      audioPremium.play().catch((error) => console.log('Error al reproducir el sonido premium:', error));
    }
    setShowConfetti(true); // Activar confetti
    setIsPremium(true);
    localStorage.setItem('isPremium', 'true'); // Guardar el estado premium en el almacenamiento local
    
    setTimeout(() => {
      setShowConfetti(false); // Desactivar confetti despus de 3 segundos
      alert('Felicidades! Ahora eres usuario Premium ');
      navigate('/menu-premium'); // Redirigir a la pgina de Menu Premium
    }, 3000); // Tiempo de espera de 3 segundos antes de redirigir
  };

  return (
    <div className="premium-container">
      {showConfetti && <Confetti numberOfPieces={300} />}
      
      <button className="back-button">
        <Link to="/">Volver al inicio</Link>
      </button>

      <h2>Bienvenido a la Experiencia Premium! </h2>

      <p>Disfruta de las funcionalidades exclusivas de la versin Premium</p>

      <div className="premium-benefits">
        <h3>Qu obtendrs con Premium?</h3>
        <ul>
          <li> Acceso a sonidos relajantes</li>
          <li> Seguimiento de progreso avanzado</li>
          <li> Acceso a contenido exclusivo</li>
          <li> Acceso a recompensas y logros</li>
          <li> Calendario personalizado</li>
        </ul>
      </div>

      <button 
        className="upgrade-button" 
        onClick={handleUpgradeToPremium}
      >
        Hazte Premium Ahora!
      </button>
    </div>
  );
}

export default Premium;
