import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import './RuidosBlancos.css';

// Rutas de los audios de los ruidos blancos
const sonidos = [
  { nombre: 'Bosque', src: require('./sounds/BEBE1.mp3') },
  { nombre: 'Cancion De Cuna', src: require('./sounds/BEBE2.mp3') },
  { nombre: 'Hora De Dormir', src: require('./sounds/BEBE3.mp3') },
  { nombre: 'Estrellita', src: require('./sounds/BEBE4.mp3') },
  { nombre: 'Beethoven', src: require('./sounds/BEBE5.mp3') }
];

function RuidosBlancos() {
  const [audioActual, setAudioActual] = useState(null);
  const [audioEnReproduccion, setAudioEnReproduccion] = useState(null);

  useEffect(() => {
    // Cleanup del audio cuando se desmonta el componente
    return () => {
      if (audioActual) {
        audioActual.pause();
        audioActual.currentTime = 0;
      }
    };
  }, [audioActual]);

  const reproducirSonido = (src, index) => {
    if (audioActual) {
      audioActual.pause();
    }
    const nuevoAudio = new Audio(src);
    nuevoAudio.play();
    setAudioActual(nuevoAudio);
    setAudioEnReproduccion(index);
  };

  const pausarSonido = () => {
    if (audioActual) {
      audioActual.pause();
      setAudioEnReproduccion(null);
    }
  };

  const detenerSonido = () => {
    if (audioActual) {
      audioActual.pause();
      audioActual.currentTime = 0;
      setAudioEnReproduccion(null);
    }
  };

  return (
    <div className="ruidos-blancos-container">
      <button className="back-button">
        <Link to="/menu-premium">Volver al Men</Link>
      </button>
      
      <h2>Ruidos Blancos </h2>
      <p>Reljate con nuestros sonidos relajantes</p>
      
      <div className="sonidos-lista">
        {sonidos.map((sonido, index) => (
          <div key={index} className="sonido-item">
            <h3>{sonido.nombre}</h3>
            <button 
              className="play-button" 
              onClick={() => reproducirSonido(sonido.src, index)}
              disabled={audioEnReproduccion === index}
            >
              {audioEnReproduccion === index ? ' Pausado' : ' Reproducir'}
            </button>
            <button 
              className="stop-button" 
              onClick={detenerSonido}
            >
               Detener
            </button>
          </div>
        ))}
      </div>
    </div>
  );
}

export default RuidosBlancos;
