import React, { useState, useEffect } from 'react';
import './App.css';
import { BrowserRouter as Router, Routes, Route, Link, useNavigate } from 'react-router-dom';
import Login from './Login';
import Diario from './Diario';  
import Momentos from './Momentos';  
import Salud from './Salud';  
import Lactancia from './Lactancia';  
import Crecimiento from './Crecimiento';  
import Premium from './Premium';  
import MenuPremium from './MenuPremium'; 
import RuidosBlancos from './RuidosBlancos';
import Calendario from './Calendario';
import Logros from './Logros';
import Confetti from 'react-confetti'; 
import logo from './images/logocreci.png';

// Importar sonido del botn
const sonidoBoton = require('./sounds/boton.mp3');

function App() {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [showIntro, setShowIntro] = useState(true);
  const [isPremium, setIsPremium] = useState(false); 
  const [isLoading, setIsLoading] = useState(true); 
  const [showConfetti, setShowConfetti] = useState(false); 
  const [audioBoton, setAudioBoton] = useState(null); 

  // Comprobar si el usuario es premium al cargar la app
  useEffect(() => {
    const premiumStatus = localStorage.getItem('isPremium');
    if (premiumStatus === 'true') {
      setIsPremium(true);
    }
    setIsLoading(false); // Terminamos la carga
  }, []);

  const handleLogin = (username, password) => {
    if (username === 'admin' && password === '1234') {
      setIsLoggedIn(true);
    } else {
      alert("Credenciales incorrectas");
    }
  };

  const handleStart = () => {
    setShowIntro(false);
    if (!audioBoton) {
      const audio = new Audio(sonidoBoton);
      setAudioBoton(audio); // Inicializar el sonido del botn
    }
  };

  // Reproducir sonido del botn
  const reproducirSonidoBoton = () => {
    if (audioBoton) {
      audioBoton.currentTime = 0; // Reiniciar el audio
      audioBoton.play().catch((error) => console.log('Error al reproducir el sonido:', error));
    }
  };

  return (
    <Router>
      <AppContent 
        isLoggedIn={isLoggedIn}
        showIntro={showIntro}
        handleStart={handleStart}
        handleLogin={handleLogin}
        isPremium={isPremium}
        setIsPremium={setIsPremium}
        showConfetti={showConfetti}
        setShowConfetti={setShowConfetti}
        isLoading={isLoading}
        reproducirSonidoBoton={reproducirSonidoBoton}
      />
    </Router>
  );
}

function AppContent({ 
  isLoggedIn, 
  showIntro, 
  handleStart, 
  handleLogin, 
  isPremium, 
  setIsPremium, 
  showConfetti, 
  setShowConfetti, 
  isLoading, 
  reproducirSonidoBoton 
}) {
  const navigate = useNavigate(); 

  // Lgica para activar Premium
  const handleSubscribePremium = () => {
    setIsPremium(true);
    localStorage.setItem('isPremium', 'true');
    setShowConfetti(true);
    setTimeout(() => setShowConfetti(false), 5000); 
    alert('Felicidades! Ahora eres usuario Premium ');
    navigate('/menu-premium'); 
  };

  // Pantalla de carga
  if (isLoading) {
    return (
      <div className="loading-screen animate-fade">
        <h2>Cargando...</h2>
      </div>
    );
  }

  // Pantalla de introduccin
  if (showIntro) {
    return (
      <div className="intro-screen animate-fade">
        <img src={logo} alt="Logo de Creci" className="logo bounce" />
        <button 
          onMouseEnter={reproducirSonidoBoton}
          onClick={handleStart} 
          className="start-button pulse"
        >
          Empezamos
        </button>
      </div>
    );
  }

  // Pantalla de inicio de sesin
  if (!isLoggedIn) {
    return <Login onLogin={handleLogin} />;
  }

  return (
    <div className="app animate-fade">
      <header className="app-header">
        <h1 className="slide-in-left">Creci</h1>
        <p>Acompandote en el arte de ser mam</p>
      </header>

      <div className="app-sections">
        {[
          { path: '/diario', label: ' Diario' },
          { path: '/momentos', label: ' Momentos' },
          { path: '/salud', label: ' Salud' },
          { path: '/lactancia', label: ' Lactancia' },
          { path: '/crecimiento', label: ' Crecimiento' },
          { path: '/premium', label: ' Hazte Premium' },
          { path: '/calendario', label: ' Calendario' },
          { path: '/logros', label: ' Logros' }
        ].map(({ path, label }, index) => (
          <Link key={index} to={path}>
            <div 
              className="section hover-bounce" 
              onMouseEnter={reproducirSonidoBoton} 
              onClick={reproducirSonidoBoton}
            >
              <span>{label.split(' ')[0]}</span>
              <p>{label.split(' ')[1]}</p>
            </div>
          </Link>
        ))}

        <a 
          href="https://wa.me/1234567890" 
          target="_blank" 
          rel="noopener noreferrer" 
          className="whatsapp-button hover-bounce"
          onMouseEnter={reproducirSonidoBoton}
          onClick={reproducirSonidoBoton}
        >
          <span></span>
          <p>WhatsApp</p>
        </a>
      </div>

      <Routes>
        <Route path="/diario" element={<Diario />} />
        <Route path="/momentos" element={<Momentos isPremium={isPremium} />} />
        <Route path="/salud" element={<Salud />} />
        <Route path="/lactancia" element={<Lactancia />} />
        <Route path="/crecimiento" element={<Crecimiento />} />
        <Route 
          path="/premium" 
          element={
            <Premium 
              setIsPremium={setIsPremium} 
              onSubscribe={handleSubscribePremium} 
            />
          } 
        />
        <Route path="/menu-premium" element={<MenuPremium />} />
        <Route path="/ruidos-blancos" element={<RuidosBlancos />} />
        <Route path="/calendario" element={<Calendario />} />
        <Route path="/logros" element={<Logros />} />
      </Routes>

      {showConfetti && <Confetti numberOfPieces={500} />}
    </div>
  );
}

export default App;
